import React from 'react';
import Content from "./Content";
import "../stylesheets/ProjectStyleSheet.css";


export default function Project({ name, contents, description, gitHubLink }) {
    var contentsComponents = null;
    if (contents) {
        contentsComponents = contents.map(
            (content, index) => <Content {...content} ></Content>);
    }

    return (
        <div className="project-container">
            <a href={gitHubLink}>
            <h2 className="project-header"> {name}</h2>
            </a>
            <p className="project-description"> {description}</p>
            <div className="project-contents-list">
                {contentsComponents}
            </div>
        </div>
    )
}