import React, { useEffect, useState } from 'react';
import './App.css';

import List from "./components/List";
import Project from "./components/Project";
import projectsFolderNames from "./projects.json";
import Spacer from './components/Spacer';

function App() {
  var [projects, setProjects] = useState([]);
  var [retrievedProjects, setRetrievedProjects] = useState("Not Retrieved");
  console.log({ projects: projects, retrievedProjects: retrievedProjects });

  if (retrievedProjects == "Not Retrieved") {
    setRetrievedProjects("Retrieving");
    GetListOfProjects(projectsFolderNames, "projects/")
      .then((projects) => {
        setProjects(projects);
        setRetrievedProjects("Retrieved");
      })
  }

  var projectsReact = projects.map((project, index) =>
    <React.Fragment key={index}>
      <Project {...project} />
      <Spacer />
    </React.Fragment>
    );


  return (
    <div className="App">
      <List>
        {projectsReact}

      </List>
    </div>
  );
}

function resolveProjectPaths(json, directory) {
  json.contents = json.contents.map((content) => {
    content.src = directory + content.src;
    return content;
  });
}

async function GetListOfProjects(projectNames, directory) {
  return Promise.all(projectNames.map(async (project) => {
    console.log(project);
    var response = await fetch(directory + project + "/content.json");
    console.log(response);
    var projectJson = await response.json();
    console.log(projectJson);
    resolveProjectPaths(projectJson, directory + project + "/");
    return projectJson;
  }));
}


export default App;
