import React from 'react';
import "../stylesheets/ContentStyleSheet.css"



export default function Content({header, description, src}){
    return <div className="content-container">
       {header? <h3 className="content-header"> {header} </h3> : null}
       {description? <p className="content-description"> {description} </p> : null}
       {src? <img className="content-img" src={src}  /> : null}
    </div>
}